import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import apiHandler from "../../api/apiHandler";
import ReviewActivity from "../Activities/ReviewActivity";

export class Activity extends Component {
  state = {
    activity: {
      title: "",
      _id: "",
      comments: [],
      marks: [],
    },
  };

  componentDidMount() {
    this.getData();
  }

  getData() {
    let activityId = this.props.match.params.id;
    apiHandler
      .getActivity(activityId)
      .then((activity) => {
        this.setState({
          activity,
        });
      })
      .catch((error) => {
        console.log(error);
      });
  }

  render() {
    const marks = this.state.activity.marks.map((mark) => mark.mark);
    const mean = marks.reduce((acc, curr) => acc + curr, 0) / marks.length;
    return (
      <div>
        {this.state.activity.title !== "" && (
          <ReviewActivity
            activity={this.state.activity}
            reload={() => this.getData()}
          />
        )}
        <div>
          {this.state.activity.comments.length > 0 &&
            this.state.activity.comments.map((comment) => (
              <div>{comment.comment ?? "gello"}</div>
            ))}
        </div>
        <div>
          {this.state.activity.marks.length > 0 &&
            this.state.activity.marks.map((mark) => (
              <div>{mark.mark ?? "c nul"}</div>
            ))}
          moyenne = {mean}
        </div>
      </div>
    );
  }
}

export default withRouter(Activity);
